img {
  width: 35%;
  border-radius: 50%;
  padding-top: 20px;
}

.wrapper {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  text-align: center;
  width:30%;
  height: 470px;
  border: 1px solid black;
  border-radius: 10px;
  background-color:rgb(167, 155, 209);
}

h1 {
  font-size: 2.5rem;
  padding-bottom: -20px;
}

p {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.footer {
  padding-top: 25px;
}

button {
  width: 120px;
  height: 30px;
  border: none;
  border-radius: 5px;
  background-color: rgb(196, 196, 212);
}

.button1 {
  margin-right: 20px;
  
}

.number {
  font-size: 2rem;
  margin-right: 10px;
}

.heart {
  font-size: 1.5rem;
  color: red;
  
}

button:hover {
  background-color:rgb(230, 51, 51);
  color: white;
  cursor: pointer;
}

#facebook {
  font-size: 1.5rem;
  color: blue;
  position: relative;
  top: 10px;
}